/*global chrome*/
import React, { Component } from 'react';
import axios from 'axios';

import storage from './storage.js';
import { makeStyles } from '@material-ui/styles';
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import ListSubheader from '@material-ui/core/ListSubheader';
import ListItem from '@material-ui/core/ListItem';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';
import Link from '@material-ui/core/Link';
import ListItemText from '@material-ui/core/ListItemText';
import Typography from '@material-ui/core/Typography';
import Fab from '@material-ui/core/Fab';
import CloseIcon from '@material-ui/icons/Close';

import './CommentDrawer.css';

function getVideoId () {
  if (window.location.href.indexOf('localhost')>=0)
    return 'xO8Cz-9qKTI';

	const urlParams = new URLSearchParams(window.location.search);
  return urlParams.get('v');
  
}
function secToTimestamp (totalSeconds) {
  let hours = Math.floor(totalSeconds / 3600);
  totalSeconds %= 3600;
  let minutes = Math.floor(totalSeconds / 60);
  let seconds = totalSeconds % 60;

  // If you want strings with leading zeroes:
  minutes = String(minutes).padStart(2, "0");
  hours = String(hours).padStart(2, "0");
  seconds = String(seconds).padStart(2, "0");
  return hours + ":" + minutes + ":" + seconds;
}

function CommentList(props) {
  const commentList = props.comments;
  const useStyles = makeStyles({
    list: {
      width: 300,
    },
    listHeader: {
      borderBottom: '1px solid lightgray',
      background: 'white',
      padding: 15
    },
    closeDrawer: {
      position: 'absolute',
      top: 12,
      left: 10,
      cursor: 'pointer',
      '&:hover': {
        color: 'black'
      }
    },
    listItem: {
      borderBottom: '1px solid lightgray',
    },
    avatar: {
      // width: 30,
      // height: 30
    },
    commentName: {
      overflow: 'hidden',
      maxWidth: 150,
      whiteSpace: 'nowrap',
      fontSize: 10,
      textOverflow: 'ellipsis',
      marginBottom: 5,
      color:'#0a0a0a',
      fontWeight: 500
    },
    commentContent: {
      color:'#111111',
      wordBreak: 'break-word'
    },
    timestamp: {
      position: 'absolute',
      right: 10,
      color: '#606060',
      fontSize: 10
    }
  });
  const classes = useStyles();

  return (
    <div>
      <List dense={true} className={classes.list}>
        <ListSubheader className={classes.listHeader}>
            <CloseIcon onClick={props.closeDrawer} className={classes.closeDrawer}/>
          <center><Typography>Comments with timestamp ({commentList.length})</Typography></center>
        </ListSubheader>

        {commentList.map((comment, index) => (
          <ListItem className={classes.listItem}  key={comment.key} alignItems="flex-start">
            <ListItemAvatar>
              <Avatar className={classes.avatar} src={comment.profileImgSrc} />
            </ListItemAvatar>
            <ListItemText 
              primary={<Typography className={classes.commentName}>{comment.name}</Typography>}
              secondary={<Typography className={classes.commentContent}>{comment.content}</Typography>}
            />
            <span className={classes.timestamp}>
              <Link style={{cursor:'pointer'}} onClick={()=>{props.commentClickHandler(comment.sec)}}>{comment.timestamp}</Link>
            </span>
          </ListItem>
        ))}
      </List>
    </div>
  );
}
class App extends Component {
  state = { 
    showDrawer: false,
    commentList: []
  };
  commentDict = {};
  lastCheckedSec = -1;
  videoId = null;
  videoPlayer = null;
  showDanmu = true; // if false then won't get new comments
  tabVisible = true;
  rootMoved = false;


  showDrawer = () => {
    this.setState({
      showDrawer: true,
    });
  }
  onClose = () => {
    this.setState({
      showDrawer: false,
    });
  }
  getYoutubeComments = () => {
    if (!(this.showDanmu && this.tabVisible)) return;
    let url = 'https://api.yiyechat.com/db/comments/youtube/';
    // let url = 'http://localhost:9000/db/comments/youtube/';
    url += this.videoId + '?pageNum=0&youtube=true';
    console.log('fetch comments');
    axios.get(url)
      .then(res => {
        let commentList = [];
        const commentDict = res.data.comments;
        Object.keys(commentDict).forEach(sec => {
          commentDict[sec].forEach( c => {
            c.sec = sec;
            c.timestamp = secToTimestamp(sec);
            c.key = c.timestamp+'-'+c.name+'-'+c.content;
            commentList.push(c);
          });
        });
        // commentList = commentList.slice(0,3);
        this.commentDict = commentDict;
        this.setState({ commentList: commentList });
        if (window.location.href.indexOf('localhost')>=0) {
            commentList.forEach(c => {
                window.queueCanvasDanmu(c);
            });
        }

      })
  }
  videoSeekTo = (sec) => {
    if (!this.videoPlayer) {
      console.error('videoPlayer is null');
      return;
    }
    window.clearVideoDanmu();
    this.videoPlayer.currentTime = sec;
  }
  registerVideoEvents = () => {
    if (!this.videoPlayer) {
        console.error('no video player');
        return;
    }
    this.videoPlayer.addEventListener('timeupdate', (e) => {
      const sec = Math.floor(e.target.currentTime);
      if (sec === this.lastCheckedSec)
        return; // avoid same sec trigger multiple times
      this.lastCheckedSec = sec;
      if (this.tabVisible && sec in this.commentDict) {
        // console.log(sec);
        this.commentDict[sec].forEach(function(comment) {
          window.queueCanvasDanmu(comment);
        })
      }
    })
    this.videoPlayer.addEventListener('pause', (e) => {
      window.setVideoState(false);
    });
    this.videoPlayer.addEventListener('play', (e) => {
      window.setVideoState(true);
    });
    // this.videoPlayer.addEventListener('seeked', (e) => {
    //   // it's better to clear danmo before seeked, otherwise won't see
    //   // danmu at seeking timestamp
    //   window.clearVideoDanmu(true);
    // });
  }
  checkVideoChanged = () => {
    const newVideoId = getVideoId();
    if (newVideoId !== this.videoId) {
      console.log('video changed from '+this.videoId+' to '+newVideoId);
      this.videoId = newVideoId;
      window.clearVideoDanmu();
      this.commentDict = {};
      this.setState({ commentList: [] });
      if (this.videoId) {
        this.getYoutubeComments();
        if (!this.videoPlayer) {
          this.videoPlayer = document.querySelector('video');
          this.registerVideoEvents(); 
        }
      } 
    }
    if (this.videoId && !this.rootMoved) {
      let youtubeViewCountQuery = document.getElementsByTagName('yt-view-count-renderer');
      if (youtubeViewCountQuery.length) {
        console.log('Found Youtube view count, move root');
        let youtubeViewCountElm = youtubeViewCountQuery[0];
        let rootElm = document.getElementById('danmu-extension-root');
        youtubeViewCountElm.appendChild(rootElm);
        this.rootMoved = true;
      }

    }
    if (this.rootMoved && !this.videoId) {
      console.log('Move root back to body');
      let rootElm = document.getElementById('danmu-extension-root');
      document.body.appendChild(rootElm);
      this.rootMoved = false;
    }




    setTimeout(this.checkVideoChanged, 3*1000);
  }
  handleTabVisibilityChange = () => {
    this.tabVisible = !document['hidden'];
  }
  componentDidMount() {
    document.addEventListener('visibilitychange', this.handleTabVisibilityChange, false);
    storage.getConfigFromStorage('videoDanmuEnabled', (showDanmu)=> {
      if (showDanmu != null) {
        this.showDanmu = showDanmu;
      }
      this.checkVideoChanged();
    })
    if (chrome.extension) {
      chrome.storage.onChanged.addListener((changes, area) => {
          if ('videoDanmuEnabled' in changes) {
            const newVal = changes['videoDanmuEnabled']['newValue'];
            console.log('videoDanmuEnabled changed '+newVal);
            this.showDanmu = newVal;
          }
      });
    }
  }
  render() {
    if (window.location.href.indexOf('youtube')>=0 || window.location.href.indexOf('localhost')>=0) {
      return (
        <span>
          { 
            <Fab style={{position: 'absolute', marginLeft: '10px', marginTop: '-5px', width: 35, height: 35, background: '#2196F3'}} color="primary" onClick={this.showDrawer}>
              {this.state.commentList.length}
            </Fab>
          }
          <Drawer ModalProps={{
            BackdropProps:{
              classes:{
                root:'sp-drawer-mask'
              }
            }
          }}
          style={{zIndex:2147483646}} anchor="right" open={this.state.showDrawer}
          onClose={this.onClose}>
            <div
              className='sp-no-outline'
              tabIndex={0}
              role="button"
              onKeyDown={this.onClose}
            >
              <CommentList comments={this.state.commentList} closeDrawer={this.onClose} commentClickHandler={this.videoSeekTo}/>
            </div>
          </Drawer>

        </span>
      );
    }
    return <div></div>
  }
}

export default App;
